<template>
    <div class="relieve-success">
        <div class="photo">
            <img :src="`${$cdnImageUrl}/login/success.png`" alt="" v-if="success">
            <img :src="`${$cdnImageUrl}/relieveResult/fail.png`" alt="" v-else>
        </div>
        <div class="relieve-content">{{result}}</div>
        <div class="relieve-http">快捷通官网:http://www/kjtpay.com</div>
        <div class="relieve-btn" @click="back">返回</div>
    </div>
</template>
<script>
export default {
  data() {
    return {
      result: '由于您营业执照中法人名称与注册个人快捷账户名称不一致，快捷通账号绑定失败，请知悉。',
      success: true,
    }
  },
  created() {
    this.getMessage()
  },
  methods: {
    /**
     * @description 返回
     */
    back() {
      this.$router.replace('/bankcard?sg_rn_app&closewebview=1')
    },
    /**
     * @description 结果获取state '0'（成功）'1'（失败）message(提示信息)
     */
    getMessage() {
      this.result = this.$route.query.message
      if (this.$route.query.state === '0') {
        this.success = true
      } else {
        this.success = false
      }
    },
  },
}
</script>
